.wishesPage {
  position: relative;
  background: url('../../../public/images/bg1.jpg') no-repeat center center/cover;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: background 2s ease-in-out;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.img1 {
  width: 300px;
  height: 300px;
  margin-top: 30vh;
  background: url('../../../public/images/heart.png') no-repeat center center/cover;
  transition: transform 2s, opacity 2s;
}
.content.clicked .img1 {
  transform: scale(2);
  opacity: 0;
}
.WishText {
  margin-top: 20px;
  width: 100vw;
  max-width: 800px;
  font-size: 1em;
  color: #fff6a9;
  text-shadow: 0 0 5px #ffa500, 
               0 0 5px #ffa500, 
               0 0 5px #ffa500, 
               0 0 14px #ffa500, 
               0 0 10px #ff0000, 
               0 0 10px #ff8d00, 
               0 0 10px #ff0000;
  font-family: "Sacramento", cursive;
  animation: typing 3s steps(40, end), blink-caret .75s step-end infinite;
}

@keyframes typing {
  from { width: 0; }
  to { width: 100%; }
}

@keyframes blink-caret {
  from, to { border-color: transparent; }
  50% { border-color: orange; }
}

.snowflakes {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1000;
}

.snowflake {
  color: #fff;
  font-size: 1em;
  font-family: Arial;
  text-shadow: 0 0 1px #000;
  position: fixed;
  top: -10%;
  z-index: 9999;
  user-select: none;
  cursor: default;
  animation-name: snowflakes-fall, snowflakes-shake;
  animation-duration: 10s, 3s;
  animation-timing-function: linear, ease-in-out;
  animation-iteration-count: infinite, infinite;
}

@keyframes snowflakes-fall {
  0% { top: -10%; }
  100% { top: 100%; }
}

@keyframes snowflakes-shake {
  0% { transform: translateX(0px); }
  50% { transform: translateX(80px); }
  100% { transform: translateX(0px); }
}

/* Individual snowflake animations */
.snowflake:nth-of-type(1) { left: 1%; animation-delay: 0s, 0s; }
.snowflake:nth-of-type(2) { left: 10%; animation-delay: 0.5s, 1s; }
.snowflake:nth-of-type(3) { left: 20%; animation-delay: 3s, 0.5s; }
.snowflake:nth-of-type(4) { left: 30%; animation-delay: 2s, 2s; }
.snowflake:nth-of-type(5) { left: 40%; animation-delay: 5s, 1.5s; }
.snowflake:nth-of-type(6) { left: 50%; animation-delay: 1s, 1s; }
.snowflake:nth-of-type(7) { left: 60%; animation-delay: 7s, 0.5s; }
.snowflake:nth-of-type(8) { left: 70%; animation-delay: 6.7s, 2s; }
.snowflake:nth-of-type(9) { left: 80%; animation-delay: 8.7s, 1s; }
.snowflake:nth-of-type(10) { left: 90%; animation-delay: 9.3s, 0s; }
.snowflake:nth-of-type(11) { left: 1%; animation-delay: 4.2s, 1s; }
.snowflake:nth-of-type(12) { left: 10%; animation-delay: 6.8s, 0.5s; }
.snowflake:nth-of-type(13) { left: 20%; animation-delay: 8.2s, 2s; }
.snowflake:nth-of-type(14) { left: 30%; animation-delay: 6.4s, 1.5s; }
.snowflake:nth-of-type(15) { left: 40%; animation-delay: 3.8s, 1s; }
.snowflake:nth-of-type(16) { left: 50%; animation-delay: 7.9s, 0.5s; }
.snowflake:nth-of-type(17) { left: 60%; animation-delay: 5s, 2s; }
.snowflake:nth-of-type(18) { left: 70%; animation-delay: 1.5s, 0s; }
.snowflake:nth-of-type(19) { left: 80%; animation-delay: 9.1s, 1s; }
.snowflake:nth-of-type(20) { left: 90%; animation-delay: 4.6s, 0.5s; }
