.CelebratePage {
    height: 100vh;
    width: 100vw;
    background: #020202;
    cursor: crosshair;
    position: relative;
  }
  
  canvas {
    display: block;
  }
  
  .content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
  
  .hbd {
    width: 350px;
    height: 350px;
    background-image: url('../../../public/images/happy-birthday.png');
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 auto;
  }
  
  h1 {
    color: #fff;
    font-family: "Source Sans Pro";
    font-size: 7em;
    font-weight: 900;
    margin-top: 20px;
    -webkit-user-select: none;
    user-select: none;
  }
  
  @media (max-width: 768px) {
    .hbd {
      width: 350px;
      height: 350px;
    }
  
    h1 {
      font-size: 5em;
    }
  }
  